@charset "UTF-8";
a, abbr, acronym, address, applet, article, aside, audio, b, big, blockquote, body, canvas, caption, center, cite, code, dd, del, details, dfn, div, dl, dt, em, embed, fieldset, figcaption, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, html, i, iframe, img, ins, kbd, label, legend, li, mark, menu, nav, object, ol, output, p, pre, q, ruby, s, samp, section, small, span, strike, strong, sub, summary, sup, table, tbody, td, tfoot, th, thead, time, tr, tt, u, ul, var, video { margin: 0; padding: 0; border: 0; font-size: 100%; font: inherit; vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section { display: block; }

body { line-height: 1; }

ol, ul { list-style: none; }

blockquote, q { quotes: none; }

blockquote:after, blockquote:before { content: ''; content: none; }

q:after, q:before { content: ''; content: none; }

table { border-collapse: collapse; border-spacing: 0; }

a { text-decoration: none; }

a:hover { text-decoration: none; }

.icon-phone { width: 1em; height: 1em; fill: initial; }

html, body { width: 100%; box-sizing: border-box; }

*, *::before, *::after { box-sizing: inherit; }

:root * { -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; -webkit-overflow-scrolling: touch; }

a { color: inherit; transition: all 0.2s ease; }

.wrapper { position: relative; font-family: "Ubuntu", Arial, sans-serif; line-height: 1.6; font-size: 15px; overflow: hidden; }

@media (max-width: 1200px) { .wrapper { font-size: calc(14.2px + 0.06667vw) ; } }

.container { margin: 0 auto; max-width: 1170px; padding-left: 1%; padding-right: 1%; }

.text-center { text-align: center; }

.circle { display: flex; justify-content: center; align-items: center; margin-right: 12px; width: 28px; height: 28px; min-width: 28px; min-height: 28px; border-radius: 50%; background-color: #fff; }

.circle img { max-width: 100%; }

.phone__icon { width: 16px; height: 16px; }

.header { min-height: 57px; width: 100%; position: absolute; top: 0; left: 0; z-index: 101; color: #fff; background-color: rgba(0, 0, 0, 0.3); }

.header-inner { padding: 13px 0 9px; display: flex; justify-content: space-between; align-items: center; }

.header__logo { max-width: 164px; }

.header__logo img { display: block; width: 100%; }

.header__right { display: flex; align-items: center; }

.header__phone { display: flex; align-items: center; font-size: 22px; font-weight: bold; color: #ffffff; }

@media (max-width: 1200px) { .header__phone { font-size: calc(15.6px + 0.53333vw) ; } }

.header__phone:hover { color: #f99d0b; }

.header__phone .phone__circle { margin-right: 13px; }

.header__lang { margin-left: 8vw; }

/** Custom Select **/
.custom__select-wrapper { width: 100px; min-width: 100px; position: relative; display: inline-block; user-select: none; }

.custom__select-wrapper select { display: none; }

.custom__select { position: relative; display: inline-block; }

.custom__select-trigger { width: 50px; height: 26px; position: relative; display: block; padding-right: 26.5px; cursor: pointer; white-space: nowrap; background-repeat: no-repeat; background-size: 25.5px 25.5px; background-position: left center; background-image: url(../img/lang-usa.svg); }

.custom__select-trigger:after { position: absolute; top: 50%; right: 0; display: block; content: ''; width: 0; height: 0; border-style: solid; border-width: 6.2px 6.2px 0 6.2px; border-color: #ffffff transparent transparent transparent; transition: all .4s ease-in-out; transform-origin: 50% 0; }

.custom__select.opened .custom__select-trigger:after { transform: rotate(-180deg) translateY(-50%); }

.custom__options { position: absolute; display: block; top: 100%; left: 0; right: 0; width: 100%; margin: 10px 0; box-sizing: border-box; transition: all .2s ease-in-out; opacity: 0; visibility: hidden; pointer-events: none; transform: translateY(-10px); background-color: rgba(255, 255, 255, 0.8); border-radius: 2px; z-index: 101; }

.custom__select.opened .custom__options { opacity: 1; visibility: visible; pointer-events: all; transform: translateY(0); }

.custom__option { position: relative; display: block; font-size: 12px; padding: 11px 10px; cursor: pointer; transition: all .2s ease-in-out; }

.custom__option:first-of-type { border-radius: 2px 2px 0 0; }

.custom__option:last-of-type { border-bottom: 0; border-radius: 0 0 2px 2px; }

.custom__option:hover { background-color: rgba(249, 157, 11, 0.3); }

.custom__option.selection { background-color: #F99D0B; }

.custom__option { height: 16px; background-repeat: no-repeat; background-size: 16px 16px; background-position: center; }

[data-value="usa"] { background-image: url(../img/lang-usa.svg); }

.hero { position: relative; padding-bottom: 8em; color: #ffffff; background-color: #3a3a3a; background-size: cover; background-position: center center; }

.hero:after { content: ""; position: absolute; bottom: 0; left: 0; width: 0; height: 0; border-style: solid; border-width: 0 0 8em 100vw; border-color: transparent transparent #fff transparent; }

@media only screen and (max-width: 992px) { .hero { padding-bottom: 10vw; }
  .hero:after { border-width: 0 0 10vw 100vw; } }

.hero__why { position: relative; padding-top: 3em; font-family: "Ubuntu", Arial, sans-serif; font-size: 50px; font-weight: bold; color: #ffffff; }

@media (max-width: 1200px) { .hero__why { font-size: calc(21.2px + 2.4vw) ; } }

.hero__why:after { content: ""; position: absolute; bottom: -10px; left: 0; display: block; width: 50px; height: 2px; background-color: #ffffff; }

.content { position: relative; padding-top: 4em; padding-bottom: 2em; }

.content__row { display: flex; justify-content: space-between; flex-wrap: wrap; }

@media only screen and (max-width: 768px) { .content__row { justify-content: center; } }

.content__column { flex-basis: 48%; margin-bottom: 1em; }

@media only screen and (max-width: 768px) { .content__column { flex-basis: 100%; } }

.content__picture { width: 100%; }

.content__title { font-family: "Ubuntu", Arial, sans-serif; font-size: 38px; line-height: 1.1; font-weight: bold; color: #0b5b8a; }

@media (max-width: 1200px) { .content__title { font-size: calc(18.8px + 1.6vw) ; } }

.content__title--underlined { position: relative; padding-bottom: 10px; }

.content__title--underlined:after { content: ""; position: absolute; bottom: -10px; left: 0; display: block; width: 50px; height: 2px; background-color: #0b5b8a; }

.content__text { padding-top: 25px; font-size: 15px; font-weight: 300; color: #373737; }

.content .content__feedback { max-width: 570px; padding: 20px 50px 30px; color: #373737; background-color: #fff; border-radius: 2px; }

.content .feedback__ratings-wrap { display: flex; align-items: flex-start; justify-content: space-around; font-size: 15px; }

.content .rating__caption { font-size: 14px; font-weight: 500; text-align: center; text-transform: uppercase; }

.content .feedback__text { padding-top: 10px; font-weight: 300; }

.content .feedback__footer { margin-top: 10px; }

.content .feedback__imre { margin-right: 5px; font-size: 20px; font-weight: bold; color: #0b5b8a; }

.content .feedback__author { font-weight: 300; }

.content--skewed:before { content: ""; position: absolute; top: 0; left: 0; display: block; height: 250px; width: 100%; transform: skew(0deg, 5deg); background-color: #EBF2F5; z-index: -1; }

.content--skewed .content__row { margin-top: 50px; }

.info { min-height: 340px; color: #ffffff; background-color: #006f9c; }

.info-inner { display: flex; align-items: flex-start; }

.info__title { padding-top: 60px; font-size: 20px; font-weight: bold; }

@media (max-width: 1200px) { .info__title { font-size: calc(15.2px + 0.4vw) ; } }

.info .contact { flex-basis: 60%; display: flex; justify-content: flex-end; position: relative; min-height: 340px; }

.info .contact .contact__data { display: flex; flex-direction: column; z-index: 10; }

.info .contact__operator { height: 108%; position: absolute; bottom: 0; left: 0; z-index: 0; }

.info .contact__item { margin-top: 10px; display: flex; }

.info .reviews { display: flex; flex-direction: column; align-items: center; }

.info .reviews__data { position: relative; display: flex; align-items: center; justify-content: center; width: 365px; text-align: center; }

.info .reviews__rating { margin: 0 2em; }

.info .reviews__mark { font-size: 80px; line-height: 1; font-weight: bold; color: #ffffff; }

.info .reviews__link { position: absolute; bottom: -15px; left: 0; width: 100%; height: auto; }

.info .reviews__link:hover { color: #f99d0b; }

.info .reviews__link:hover span { border-color: #f99d0b; }

.info .reviews__link span { border-bottom: 1px solid #fff; }

.info .reviews__pic--left { -moz-transform: scaleX(-1); -o-transform: scaleX(-1); -webkit-transform: scaleX(-1); transform: scaleX(-1); filter: FlipH; -ms-filter: "FlipH"; }

.footer { color: #fff; background-color: #21547d; }

.footer .footer__menu { padding: 3em 0 2em; display: flex; flex-wrap: wrap; }

@media only screen and (max-width: 768px) { .footer .footer__menu { justify-content: space-around; } }

@media only screen and (max-width: 480px) { .footer .footer__menu { flex-direction: column; align-items: center; } }

.footer .menu__column { margin-bottom: 1em; margin-right: 10vw; }

.footer .menu__column:last-child { margin: 0; }

@media only screen and (max-width: 768px) { .footer .menu__column { margin-right: 0; flex-basis: 35%; } }

@media only screen and (max-width: 480px) { .footer .menu__column { flex-basis: 100%; text-align: center; } }

.footer .menu__title { margin-bottom: 0.6em; font-size: 18px; font-weight: bold; color: #f58b0e; }

@media (max-width: 1200px) { .footer .menu__title { font-size: calc(14.8px + 0.26667vw) ; } }

@media only screen and (max-width: 768px) { .footer .menu__title { margin-bottom: 0.1em; } }

.footer .menu__link { font-size: 14px; }

.footer .menu__link:hover { color: #f99d0b; }

.footer .footer__copy { padding: 12px 0; text-align: center; text-transform: uppercase; font-size: 12px; font-weight: bold; color: #ffffff; background-color: #184468; }

.stars { display: inline-block; position: relative; font-size: 17px; line-height: 1; letter-spacing: 5px; }

.stars:BEFORE { content: "★★★★★"; position: absolute; overflow: hidden; z-index: 1; left: 0px; top: 0px; color: #f99d0b; -webkit-text-stroke-width: 1px; -webkit-text-stroke-color: #f99d0b; }

.stars:AFTER { content: "★★★★★"; position: relative; color: #fff; -webkit-text-stroke-width: 1px; -webkit-text-stroke-color: #f99d0b; }

.stars:NOT([data-percent]):BEFORE, .stars[data-percent="0"]:BEFORE { display: none; }

/* 1/2 a star */
.stars[data-percent="10"]:BEFORE { width: 10%; }

/* 1 star */
.stars[data-percent="20"]:BEFORE { width: 20%; }

/* 1 & 1/2 stars */
.stars[data-percent="30"]:BEFORE { width: 30%; }

.stars[data-percent="40"]:BEFORE { width: 40%; }

.stars[data-percent="50"]:BEFORE { width: 50%; }

.stars[data-percent="60"]:BEFORE { width: 60%; }

.stars[data-percent="70"]:BEFORE { width: 70%; }

.stars[data-percent="80"]:BEFORE { width: 80%; }

.stars[data-percent="90"]:BEFORE { width: 90%; }

.stars[data-percent="100"]:BEFORE { width: 100%; }

